import axios from 'axios';

const apiClient = axios.create({
	baseURL: 'http://<SERVER_IP>:5000/api/',
	// baseURL: 'https://api.gamersoffice.co/endpoints/',
	withCredentials: false,
	headers: {
		'Accept': 'application/json',
		'Content-Type': 'application/json'
	}
});

export default {
	/****************
	*** Dashboard ***
	****************/
	
	// getHosts() {
	// 	return apiClient.get('/data/montages.php?action=requestMontage&montageObj=' + encodeURIComponent(JSON.stringify(obj)) + '&user_id=' + store.state.user.id + '&token=' + store.state.user.token);
	// },

	getHosts() {
		return apiClient.get('/hosts/list');
	},

	getCommands(host_id) {
		return apiClient.get('/commands/list', {
			params: {
				'host_id' : host_id
			}
		});
	},
	
    addCommandToHost(host_id, cmd) {
		return apiClient.post('/commands/add', [host_id, cmd]);
	},


	logout(token) {
		return apiClient.get('/auth/logout.php?token=' + token);
	}
}